.header {
	padding: 20px 0;
	color: $black;
	&__container {
		font-size: 0; }
	&__col {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		padding: 0 15px;
		&:first-child {
			padding-left: 0; }
		&:last-child::after {
			display: none; }
		&::after {
			content: '';
			position: absolute;
			right: 0;
			top: 0;
			background: #e6e7e8;
			height: 41px;
			width: 1px; } }
	&__logo {
		display: block;
		@include s('logo'); }
	&__phone {
		text-align: center;
		&-link {
			display: block;
			font-size: 22px;
			font-weight: 500; }
		&-text {
			color: $grey;
			font-size: 13px;
			font-weight: 300; } }
	&__bar {
		&-col {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			padding-right: 30px;
			&:last-child {
				padding-right: 0; }
			&:last-child::after {
				display: none; }
			&::after {
				position: absolute;
				right: 15px;
				top: 0;
				content: '';
				background: #e6e7e8;
				height: 41px;
				width: 1px; } }
		&-link {
			position: relative;
			display: block;
			&_mail {
				@include s('mail'); }
			&_cart {
				@include s('cart'); }
			&_user {
				@include s('user'); } }
		&-count {
			position: absolute;
			top: -3px;
			right: -5px;
			width: 16px;
			height: 16px;
			background-color: #d62326;
			color: $white;
			font-size: 8px;
			font-weight: 700;
			line-height: 16px;
			text-align: center;
			border-radius: 50%; } }

	&__title {
		@include hide-text; } }

.nav {
	font-size: 0;
	&__link {
		position: relative;
		display: inline-block;
		padding: 3px;
		vertical-align: middle;
		font-size: 16px;
		font-weight: 700;
		border-bottom: 2px solid  #e7e8e9;
		&::after {
			content: '';
			position: absolute;
			left: 50%;
			right: 50%;
			bottom: -2px;
			background: #e7e8e9;
			height: 2px;
			@include tr(.3s ease); }
		&:hover::after {
			left: 0;
			right: 0;
			background: $red; }
		&:not(:last-child) {
			margin-right: 20px; }
		&.is-active {
			border-bottom-color: $red;
			&::after {
				background: $red; } } } }
