  fill: none;
}

.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  position: relative;
  z-index: 1;
}

.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
}

.owl-carousel .owl-stage:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  -webkit-transform: translate3d(0px, 0px, 0px);
}

.owl-carousel .owl-wrapper {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
}

.owl-carousel .owl-item {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  position: relative;
  min-height: 1px;
  float: left;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}

.owl-carousel .owl-item img {
  display: block;
  width: 100%;
}

.owl-carousel .owl-nav.disabled, .owl-carousel .owl-dots.disabled {
  display: none;
}

.owl-carousel .owl-nav .owl-prev, .owl-carousel .owl-nav .owl-next {
  cursor: pointer;
  cursor: hand;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.owl-carousel .owl-dot {
  cursor: pointer;
  cursor: hand;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.owl-carousel.owl-loaded {
  display: block;
}

.owl-carousel.owl-loading {
  opacity: 0;
  display: block;
}

.owl-carousel.owl-hidden {
  opacity: 0;
}

.owl-carousel.owl-refresh .owl-item {
  visibility: hidden;
}

.owl-carousel.owl-drag .owl-item {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.owl-carousel.owl-grab {
  cursor: move;
  cursor: grab;
}

.owl-carousel.owl-rtl {
  direction: rtl;
}

.owl-carousel.owl-rtl .owl-item {
  float: right;
}

.no-js .owl-carousel {
  display: block;
}

.owl-carousel .animated {
  -webkit-animation-duration: 1000ms;
  animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.owl-carousel .owl-animated-in {
  z-index: 0;
}

.owl-carousel .owl-animated-out {
  z-index: 1;
}

.owl-carousel .fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.owl-height {
  transition: height 500ms ease-in-out;
}

.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  transition: opacity 400ms ease;
}

.owl-carousel .owl-item img.owl-lazy {
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000;
}

.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  background: url("owl.video.play.png") no-repeat;
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  transition: -webkit-transform 100ms ease;
  transition: transform 100ms ease;
}

.owl-carousel .owl-video-play-icon:hover {
  -webkit-transform: scale(1.3, 1.3);
  -ms-transform: scale(1.3, 1.3);
  transform: scale(1.3, 1.3);
}

.owl-carousel .owl-video-playing .owl-video-tn, .owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none;
}

.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 400ms ease;
}

.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
  height: 100%;
  width: 100%;
}

.header {
  padding: 20px 0;
  color: #000;
}

.header__container {
  font-size: 0;
}

.header__col {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  padding: 0 15px;
}

.header__col:first-child {
  padding-left: 0;
}

.header__col:last-child::after {
  display: none;
}

.header__col::after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  background: #e6e7e8;
  height: 41px;
  width: 1px;
}

.header__logo {
  display: block;
  background-image: url("../img/sprite.png");
  width: 162px;
  height: 29px;
  background-position: 0px -167px;
}

.header__phone {
  text-align: center;
}

.header__phone-link {
  display: block;
  font-size: 22px;
  font-weight: 500;
}

.header__phone-text {
  color: #4d4d4f;
  font-size: 13px;
  font-weight: 300;
}

.header__bar-col {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  padding-right: 30px;
}

.header__bar-col:last-child {
  padding-right: 0;
}

.header__bar-col:last-child::after {
  display: none;
}

.header__bar-col::after {
  position: absolute;
  right: 15px;
  top: 0;
  content: '';
  background: #e6e7e8;
  height: 41px;
  width: 1px;
}

.header__bar-link {
  position: relative;
  display: block;
}

.header__bar-link_mail {
  background-image: url("../img/sprite.png");
  width: 30px;
  height: 23px;
  background-position: -231px -150px;
}

.header__bar-link_cart {
  background-image: url("../img/sprite.png");
  width: 28px;
  height: 25px;
  background-position: -231px -77px;
}

.header__bar-link_user {
  background-image: url("../img/sprite.png");
  width: 25px;
  height: 28px;
  background-position: -231px -112px;
}

.header__bar-count {
  position: absolute;
  top: -3px;
  right: -5px;
  width: 16px;
  height: 16px;
  background-color: #d62326;
  color: #fff;
  font-size: 8px;
  font-weight: 700;
  line-height: 16px;
  text-align: center;
  border-radius: 50%;
}

.header__title {
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
}

.nav {
  font-size: 0;
}

.nav__link {
  position: relative;
  display: inline-block;
  padding: 3px;
  vertical-align: middle;
  font-size: 16px;
  font-weight: 700;
  border-bottom: 2px solid  #e7e8e9;
}

.nav__link::after {
  content: '';
  position: absolute;
  left: 50%;
  right: 50%;
  bottom: -2px;
  background: #e7e8e9;
  height: 2px;
  transition: 0.3s ease;
}

.nav__link:hover::after {
  left: 0;
  right: 0;
  background: #de4e4e;
}

.nav__link:not(:last-child) {
  margin-right: 20px;
}

.nav__link.is-active {
  border-bottom-color: #de4e4e;
}

.nav__link.is-active::after {
  background: #de4e4e;
}

.benefits__title {
  text-align: center;
}

.benefits__list {
  margin: 20px 0;
  font-size: 0;
  text-align: center;
}

.benefits__item {
  display: inline-block;
  vertical-align: top;
  padding: 0 20px 30px;
  text-align: center;
  width: 33.33333%;
}

.benefits__item-pic {
  height: 100px;
  text-align: center;
  font-size: 0;
}

.benefits__item-pic:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.benefits__item-pic::after {
  content: '';
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
}

.benefits__item-pic_tape::after {
  background-image: url("../img/sprite.png");
  width: 65px;
  height: 81px;
  background-position: -117px -62px;
}

.benefits__item-pic_peoples::after {
  background-image: url("../img/sprite.png");
  width: 107px;
  height: 73px;
  background-position: 0px 0px;
}

.benefits__item-pic_building::after {
  background-image: url("../img/sprite.png");
  width: 62px;
  height: 67px;
  background-position: -231px 0px;
}

.benefits__item-pic_vehicle::after {
  background-image: url("../img/sprite.png");
  width: 104px;
  height: 52px;
  background-position: -117px 0px;
}

.benefits__item-pic_handshake::after {
  background-image: url("../img/sprite.png");
  width: 87px;
  height: 74px;
  background-position: 0px -83px;
}

.benefits__item-title {
  font-size: 22px;
  font-weight: 700;
  color: #000;
}

.benefits__item-sub-title {
  font-size: 16px;
  font-weight: 500;
  color: #4d4d4f;
}

.benefits__item-descr {
  font-size: 16px;
  color: #4d4d4f;
}

.benefits_default {
  padding: 65px 0;
}

.testimonials__container {
  *zoom: 1;
}

.testimonials__container:after {
  content: " ";
  display: table;
  clear: both;
}

.testimonials__col {
  float: left;
  width: 50%;
}

.testimonials__slider-item {
  position: relative;
  padding-top: 131%;
  z-index: 0;
  cursor: grab;
}

.testimonials__slider-title {
  position: absolute;
  top: 10%;
  left: 0;
  right: 0;
  z-index: 1;
  padding: 0 15px;
  text-align: center;
}

.testimonials__slider-title mark {
  display: block;
  color: #de4e4e;
  background: transparent;
}

.testimonials__slider-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  z-index: -1;
}

.testimonials__slider-link {
  position: absolute;
  left: 50%;
  bottom: 12%;
  transform: translateX(-50%);
  z-index: 1;
}

.testimonials__slider-icon {
  font-size: 43px;
  fill: none;
  stroke: #4d4d4f;
}

.testimonials__slider-btn {
  position: absolute;
  top: calc(50% - 7px);
  z-index: 1;
  padding: 5px;
  cursor: pointer;
}

.testimonials__slider-btn_prev {
  left: 30px;
}

.testimonials__slider-btn_next {
  right: 30px;
}

.testimonials__block-item {
  position: relative;
  padding: 15px;
  border-bottom: 1px dashed #e0e1e2;
}

.testimonials__block-link {
  font-size: 12px;
  font-weight: 700;
}

.testimonials__block-name {
  display: inline-block;
  vertical-align: middle;
  margin-left: 25px;
  font-weight: 500;
}

.testimonials__block-row {
  margin-top: 10px;
}

.testimonials__block-quote {
  position: absolute;
  fill: #de4e4e;
}

.testimonials__block-quote_l {
  top: 0;
  left: 0;
}

.testimonials__block-quote_r {
  right: 0;
  bottom: 0;
}

.testimonials__block-bottom {
  margin: 30px 0;
  text-align: center;
}

body {
  font-family: 'Roboto', Arial, sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.4375;
  color: #4d4d4f;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  display: block;
}

.out {
  overflow: hidden;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.title-md {
  font-size: 32px;
  font-weight: 500;
  line-height: 1.625;
  text-transform: uppercase;
  color: #000;
}

.link {
  color: #de4e4e;
  border-bottom: 1px solid #de4e4e;
}

.no-touch .link:hover {
  border-bottom-color: transparent;
}

.link_upper {
  text-transform: uppercase;
}

.btn {
  display: inline-block;
  height: 45px;
  padding: 0 24px;
  border-radius: 2px;
  background-color: #fff;
  border: 2px solid #de4e4e;
  outline: #de4e4e solid 1px;
  outline-offset: -2px;
  transition: all 0.7s cubic-bezier(0.19, 1, 0.22, 1);
  text-transform: uppercase;
  color: #000;
  font-size: 14px;
  font-weight: 700;
  line-height: 44px;
  cursor: pointer;
}

.no-touch .btn:hover {
  outline-color: rgba(255, 255, 255, 0);
  outline-offset: 12px;
  background: #de4e4e;
  color: #fff;
}

.sect-bg {
  background: url("../img/bg/sect-bg.jpg") repeat 50% 50%;
}

.js-next-section {
  position: relative;
}

.next-section {
  position: absolute;
  left: calc(50% - 12px);
  bottom: 40px;
  z-index: 1;
  background: 0 0;
}

.next-section__icon {
  font-size: 58px;
}
