.benefits {
	&__title {
		text-align: center; }
	&__list {
		margin: 20px 0;
		font-size: 0;
		text-align: center; }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 0 20px 30px;
		text-align: center;
		width: 33.33333%;
		&-pic {
			height: 100px;
			@include vertical;
			&::after {
				content: '';
				display: inline-block;
				vertical-align: middle;
				margin: 0 auto; }
			&_tape::after {
				@include s('tape'); }
			&_peoples::after {
				@include s('peoples'); }
			&_building::after {
				@include s('building'); }
			&_vehicle::after {
				@include s('vehicle'); }
			&_handshake::after {
				@include s('handshake'); } }
		&-title {
			font-size: 22px;
			font-weight: 700;
			color: $black; }
		&-sub-title {
			font-size: 16px;
			font-weight: 500;
			color: $grey; }
		&-descr {
			font-size: 16px;
			color: $grey; } }
	&_default {
		padding: 65px 0; } }
